//------------------------------------------------------------------------
// Hero
//------------------------------------------------------------------------
.Hero {
  $bp-wide: 768px;
  $bp-end-scale: 1440px;
  $img-width-vw: 40.5vw;
  @include fs-font-smoothing;
  @include fs-scale(padding-top, ($bp-wide: 100px, $bp-end-scale: 246px), $initial: false);
  overflow: hidden;
  padding: fs-rem(100px 42px 53px);
  position: relative;

  @include fs-min-width($bp-wide) {
    display: flex;
    justify-content: space-between;
    padding-left: fs-rem($layout-padding-max);
    padding-right: fs-rem($layout-padding-max);
  }

  @media print {
    padding: 0;
  }

  &-image {
    @include fs-print-hide;
    position: relative;

    @include fs-min-width($bp-wide) {
      position: absolute;
      right: -9px;
      top: 50%;
      transform: translateY(-50%);
      width: $img-width-vw;
    }

    img {
      object-fit: contain;
      margin-left: auto;
      margin-right: auto;
      mask-image: fs-svg-url('hero-mask');
      mask-size: 100% 100%;
      max-width: fs-rem(380px);
      width: 100%;

      @include fs-min-width($bp-wide) {
        margin-right: -9px;
        max-width: fs-rem(584px);
      }
    }

    &-bubbles {
      @include fs-print-hide;

      svg {
        fill: #fff;
        height: 133%;
        left: 50%;
        object-fit: contain;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -56%);
        width: 107%;
      }
    }// end bubbles
  }// end image

  &-topBubble {
    @include fs-print-hide;

    @include fs-max-width($bp-wide - 1px) {
      display: none;
    }

    @include fs-min-width($bp-end-scale) {
      display: none;
    }

    svg {
      left: 50%;
      position: absolute;
      top: 0;
      transform: translateY(-50%);
      width: 100vw * (131px / 1440px);
    }
  }// end topBubble

  &-wrap {
    @include fs-scale(margin-top, (375px: 0, $bp-wide: 30px));

    @include fs-min-width($bp-wide) {
      align-items: center;
      display: flex;
      justify-content: space-between;
      margin-top: 0;
      width: 100%;
    }

    @media print {
      max-width: none !important;
    }
  }// end wrap

  &-content {
    max-width: fs-rem(665px);

    @include fs-min-width($bp-wide) {
      // Account for absolutely-positioned image width
      width: calc(#{100vw - $img-width-vw} - #{$layout-padding-max});
    }

    @include fs-min-width($layout-max-width-wide-bp) {
      // We need to subtract the padding and extra space beyond ‚Äúl-width--wide‚Äù from (100vw -  $img-width-vw)
      // Here‚Äôs the full formula:
      // width: calc(#{100vw - $img-width-vw} - #{$layout-padding-max} - (50vw - #{($layout-max-width-wide / 2) + $layout-padding-max}));
      // Below is the simplified formula:
      width: calc(#{100vw - $img-width-vw - 50vw} + #{$layout-max-width-wide / 2});
    }

    @media print {
      max-width: none !important;
      width: 100% !important;
    }
  }// end content

  &-title {
    $text-shadow-color: rgba(darken($bg-orange, 33%), 0.5);
    @include fs-scale(font-size, (
      320px: 23px,
      $bp-wide: (62px 30px),
      $bp-end-scale: 62px
    ));
    font-weight: $manrope-extrabold;
    line-height: 1.06;
    text-shadow: 0 0 3px $text-shadow-color;// improve legibility

    @media print {
      font-size: 30pt;
    }

    &-logo {
      --icon-color: #fff;
      @include fs-scale(width, (375px: 265px, $bp-end-scale: 445px));
      filter: drop-shadow(0 0 1px $text-shadow-color);
      display: block;
      height: auto;
      margin-bottom: fs-rem(32px);
      max-width: 100%;

      @media print {
        --icon-color: #000;
      }
    }// end icon
  }// end title

  &-cta {
    @include fs-print-hide;
    margin-top: fs-rem(32px);
  }// end cta

  &-arrow {
    @include fs-print-hide;
    margin-left: fs-rem(174px);
    margin-top: fs-rem(26px);
    width: fs-rem(37px);

    @include fs-max-width($layout-nav-bp - 1px) {
      display: none;
    }

    // Note: SVG has ‚Äúfill‚Äù attribute inlineon the <path> elements
    svg {
      display: block;
      width: 100%;
    }

    // Animate arrow
    // 1. Reset ‚Äústroke-linecap‚Äù before animation begins to hide dot
    .arrow {
      &-line {
        @media not screen and (prefers-reduced-motion: reduce) {
          @include drawArrow;
          opacity: 0;// will be overridden by the animation
          stroke-dasharray: 1;
          stroke-dashoffset: 1;
          stroke-linecap: butt;// [1]
          transition: none;
        }
      }// end line

      &-head {
        @media not screen and (prefers-reduced-motion: reduce) {
          @include fadeInArrowHead;
          opacity: 0;
        }
      }// end head
    }
  }// end arrow
}
