//------------------------------------------------------------------------
// Graph legends (used by line and doughnut charts)
//------------------------------------------------------------------------
.Legend {
  $bp-end-scale: 640px;
  $bp-wide: 768px;

  &-item {
    @include fs-scale(font-size, (375px: 16px, $bp-end-scale: 20px));
    align-items: center;
    display: flex;
    line-height: 1.25;

    &:not(:first-child) {
      margin-top: fs-rem(26px);
    }

    @include fs-min-width($bp-wide) {
      flex-shrink: 0;
    }
  }// end item

  &-dot {
    $size: 24px;
    border: ($size / 2) solid var(--theme-color);
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: fs-rem(15px);
  }// end dot

  &-value {
    font-weight: $manrope-extrabold;
  }// end value

  &-text {}

}// end Legend
