//------------------------------------------------------------------------
// Chapter banner
//------------------------------------------------------------------------
.ChapterBanner {
  $bp-wide: 768px;
  $bp-end-scale: 1440px;
  $vert-img-offset-mobile: 34px;
  $vert-img-offset-desktop: 69px;
  $horz-image-offset-desktop: 134px;
  position: relative;// for bubbles

  @include fs-max-width($bp-wide - 1px) {
    margin-bottom: fs-rem(-$vert-img-offset-mobile);// account for negative top offset on wrapper
    margin-top: fs-rem(39px + $vert-img-offset-mobile);
    padding-bottom: fs-rem(35px - $vert-img-offset-mobile);
  }

  @include fs-min-width($bp-wide) {
    margin-top: fs-rem(110px + $vert-img-offset-desktop);

    &.no-decorations {
      margin-top: fs-rem($vert-img-offset-desktop);
    }
  }

  @media print {
    margin-bottom: fs-rem(50px);
    page-break-before: always;
  }

  &-decorations {
    @include fs-print-hide;

    @include fs-max-width($bp-wide - 1px) {
      display: none;
    }

    svg {
      bottom: 100%;
      height: auto;
      position: absolute;
      z-index: -1;
    }

    &-left {
      $left-width: 450px;
      left: -83px;
      transform: translateY(47px);
      max-width: fs-rem($left-width);
      width: 100vw * ($left-width / 1440px);
    }// end left

    &-right {
      $right-width: 245px;
      max-width: fs-rem($right-width);
      right: 18px;
      transform: translate(-22px);
      width: 100vw * ($right-width / 1440px);
    }// end right
  }// end decorations

  &-wrap {
    @include fs-max-width($bp-wide - 1px) {
      position: relative;
      top: fs-rem(-$vert-img-offset-mobile);
    }

    @include fs-min-width($bp-wide) {
      display: flex;
      justify-content: space-between;
    }
  }// end wrap

  &-image {
    @include fs-print-hide;
    @include fs-scale(margin-right, (
      $layout-max-width-wide-bp: 0px,
      $bp-end-scale: -$horz-image-offset-desktop
    ), $initial: false);
    max-width: fs-rem(360px);

    @include fs-max-width($bp-wide - 1px) {
      margin-bottom: fs-rem(11px);
      margin-left: auto;
      margin-right: auto;
    }

    @include fs-min-width($bp-wide) {
      flex-shrink: 0;
      order: 2;
      margin-left: fs-rem(44px);
      margin-top: fs-rem(-$vert-img-offset-desktop);
      max-width: fs-rem(631px);
      width: 38vw;
    }

    @include fs-min-width(900px) {
      width: 43vw;
    }

    img {
      object-fit: contain;
      mask-image: fs-svg-url('chapter-mask');
      mask-size: 100% 100%;
      width: 100%;
    }
  }// end image

  &-content {
    align-items: flex-start;
    display: flex;
    flex-direction: column;

    @include fs-min-width($bp-wide) {
      padding-top: fs-rem(50px);
    }
  }// end content

  &-title {
    @include fs-scale(font-size, (
      375px: 30px,
      $bp-wide: (45px 30px),
      $bp-end-scale: 45px
    ));
    break-inside: avoid;
    font-weight: $manrope-extrabold;
    line-height: 1.2;
  }// end title

  &-description {
    @include fs-scale(font-size, (
      375px: 16px,
      $bp-wide: (24px 16px),
      $bp-end-scale: 24px
    ));
    break-inside: avoid;
    font-weight: $manrope-semibold;
    line-height: 1.3;
    margin-top: fs-rem(23px);

    @include fs-min-width($bp-wide) {
      margin-top: fs-rem(28px);
    }
  }// end description

  &-share {
    margin-bottom: fs-rem(64px);
    margin-top: fs-rem(30px);
  }

}// end ChapterBanner
