//------------------------------------------------------------------------
// Card
//------------------------------------------------------------------------
.Card {
  $max-width: 540px;
  @include fs-scale(padding-left padding-right, (375px: 28px, $max-width: 33px));
  @include fs-scale(padding-top, (375px: 30px, $max-width: 40px));
  align-items: flex-start;
  background-color: $bg-default;
  // background-color: rgba($bg-default, 0.3);
  // backdrop-filter: blur(20px);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  min-height: fs-rem(310px);
  padding-bottom: fs-rem(30px);

  @media print {
    border: 1px solid #000;
    break-inside: avoid;
    min-height: 0 !important;
  }

  // Wait a bit before setting ‚Äúmax-width‚Äù to make it look intentional
  @include fs-min-width($max-width + ($layout-padding-max * 2) + 30px) {
    max-width: fs-rem($max-width);
  }

  & + & {
    margin-top: fs-rem(27px);
  }

  &:not(.has-border) {
    box-shadow: $box-shadow-default;
  }

  &.has-border {
    border: 4px solid var(--theme-color, #{$bg-orange});

    & + & {
      margin-top: fs-rem(11px);
    }

    @media print {
      border: 1px solid currentColor;
    }
  }

  &-text {
    font-size: fs-rem(24px);
    font-weight: $manrope-semibold;
    line-height: 1.125;
    margin-bottom: fs-rem(30px);

    b,
    strong {
      font-size: fs-rem(29px);
      font-weight: $manrope-extrabold;
    }
  }// end text

  // Has ‚Äú.BarChart-bar‚Äù class
  &-bar {
    &:not(:first-child) {
      margin-top: fs-rem(36px);
    }

    &:not(:last-child) {
      margin-bottom: fs-rem(30px);
    }
  }// end bar

  &-description {
    font-size: fs-rem(16px);
    line-height: 1.36;
    margin-bottom: fs-rem(30px);
    margin-top: auto;// bottom align
  }// end description

  &-share {
    @include fs-print-hide;
    align-items: center;
    display: flex;
    margin-left: auto;
    margin-top: auto;

    &-label {
      font-size: fs-rem(14px);
      font-weight: bold;
      line-height: 1.2;
      margin-right: fs-rem(10px);
    }// end label

    &-link {
      $size: 38px;
      --icon-color: var(--theme-text-color);
      background-color: var(--theme-color);
      border-radius: 50%;
      height: fs-rem($size);
      position: relative;
      width: fs-rem($size);

      &:not(:last-child) {
        margin-right: fs-rem(12px);
      }

      @include fs-attention {
        transform: scale(1.1);
      }
    }// end link

    .icon {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }// end share
}// end Card
