//------------------------------------------------------------------------
// Web fonts
// Note: Only woff and woff2 are required for modern browsers
// https://css-tricks.com/snippets/css/using-font-face/#article-header-id-1
//
// In the future, we could consider not loading fonts if ‚Äúdata-saver‚Äù mode is enabled
// https://css-tricks.com/responsible-conditional-loading/
// https://caniuse.com/mdn-css_at-rules_media_prefers-reduced-data
//------------------------------------------------------------------------

// We‚Äôre using ‚Äúfont-display: fallback‚Äù as a compromise between
// performance and minimizing the chances of a FOIT or FOUC.
//
//   ‚ÄúThe browser will hide the text for about 100ms and, if the font
//   has not yet been downloaded, will use the fallback text. It will
//   swap to the new font after it is downloaded, but only during a
//   short swap period (probably 3 seconds).‚Äù
//   https://css-tricks.com/almanac/properties/f/font-display/
//
$font-display: fallback;

// Custom emoji font to standardize appearance across platforms
// https://www.client9.com/css-color-emoji-stack/
@font-face {
  font-family: "color-emoji";
  src: local("Apple Color Emoji"),
       local("Segoe UI Emoji"),
       local("Segoe UI Symbol"),
       local("Noto Color Emoji");
}

// Manrope
// https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&family=Manrope:wght@400;600;700;800&display=swap
// https://google-webfonts-helper.herokuapp.com/fonts/manrope?subsets=latin
//---------------------------------
@font-face {
  font-display: $font-display;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/manrope/manrope-400.woff2") format("woff2"),
       url("../fonts/manrope/manrope-400.woff") format("woff");
}

@font-face {
  font-display: $font-display;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/manrope/manrope-600.woff2") format("woff2"),
       url("../fonts/manrope/manrope-600.woff") format("woff");
}

@font-face {
  font-display: $font-display;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/manrope/manrope-700.woff2") format("woff2"),
       url("../fonts/manrope/manrope-700.woff") format("woff");
}

@font-face {
  font-display: $font-display;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/manrope/manrope-800.woff2") format("woff2"),
       url("../fonts/manrope/manrope-800.woff") format("woff");
}

// Caveat
// https://fonts.google.com/specimen/Caveat
//---------------------------------
@font-face {
  font-display: $font-display;
  font-family: "Caveat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/caveat/caveat-v14-latin-regular.woff2") format("woff2"),
       url("../fonts/caveat/caveat-v14-latin-regular.woff") format("woff");
}
