//------------------------------------------------------------------------
// Theme color classes
//------------------------------------------------------------------------
.t {
  @each $name, $color in $theme-colors {
    &-#{$name} {
      --theme-color: #{$color};
      // Use dark text on light backgrounds (except orange which uses white text)
      @if index(('gold', 'yellow'), $name) {
        --theme-text-color: #{$c-navy};
      }
      @else {
        --theme-text-color: #fff;
      }
    }
  }
} // end t
