//------------------------------------------------------------------------
// Copy link button
//------------------------------------------------------------------------
.CopyLink {
  $text-fade-speed: 150ms;
  --icon-bg-color: #{$bg-orange};
  --icon-color: #fff;
  @include link-underline-hover($thickness: 1px);
  @include fs-print-hide;
  align-items: center;
  color: var(--theme-text-color);
  display: flex;
  font-size: fs-rem(16px);
  font-weight: bold;

  // Hide underline when link is not hovered, even if it‚Äôs focused/active,
  // unless using the keyboard (like old button-attention() mixin).
  &:not(:hover):not(:focus-visible) {
    text-decoration: none;
  }

  .icon {
    flex-shrink: 0;
    margin-right: fs-rem(10px);
  }

  .text {
    overflow: hidden;
  }

  // Toggle button text when copied to clipbord
  .default {
    opacity: 1;
    transition: opacity $text-fade-speed ease-in-out,
                max-height 0s linear,
                max-width 0s linear,
                visibility 0s linear;
  }

  // Hide
  .success {
    max-height: 0;
    max-width: 0;
    opacity: 0;
    transition: opacity $text-fade-speed ease-in-out,
                max-height 0s linear $text-fade-speed,
                max-width 0s linear $text-fade-speed,
                visibility 0s linear $text-fade-speed;
    visibility: hidden;
  }

  &.copied {
    // Hide
    .default {
      max-height: 0;
      max-width: 0;
      opacity: 0;
      transition: opacity $text-fade-speed ease-in-out,
                  max-height 0s linear $text-fade-speed,
                  max-width 0s linear $text-fade-speed,
                  visibility 0s linear $text-fade-speed;
      visibility: hidden;
    }

    // Show
    .success {
      max-height: none;
      max-width: none;
      opacity: 1;
      transition: opacity $text-fade-speed ease-in-out,
                  max-height 0s linear,
                  max-width 0s linear,
                  visibility 0s linear;
      visibility: visible;
      width: 100%;
    }
  }
}// end CopyLink
