//------------------------------------------------------------------------
// Rich text heading styles
//
// Note: Be aware that ‚Äútext-rendering: optimizeLegibility‚Äù can sometimes
//       result in edge-case issues. Make sure to test if using it.
//------------------------------------------------------------------------
@mixin h2($color: $c-default) {
  @include fs-scale(font-size, (360px: 36px, $layout-text-scale-end-bp: 45px));
  color: $color;
  font-weight: $manrope-extrabold;
  line-height: 1.2;
}

@mixin h3($color: $c-default) {
  @include fs-scale(font-size, (360px: 23px, $layout-text-scale-end-bp: 32px));
  color: $color;
  font-weight: $manrope-extrabold;
  line-height: 1.2;
}

@mixin h4($color: $c-default) {
  @include fs-scale(font-size, (360px: 18px, $layout-text-scale-end-bp: 25px));
  color: $color;
  font-weight: $manrope-extrabold;
  line-height: 1.2;
}
