//------------------------------------------------------------------------
// Doughnut chart grid
//------------------------------------------------------------------------
.DoughnutChartGrid {
  $bp-row: 1024px;
  $bp-end-scale: $layout-max-width-wide-bp;
  $gutter: 40px;
  margin-top: fs-rem(30px);

  @include fs-min-width($bp-row) {
    align-items: stretch;
    column-gap: fs-rem(40px);
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: fs-rem(80px);
  }

  &-item {
    @include fs-max-width($bp-row - 1px) {
      &:not(:first-child) {
        margin-top: 10.6%;
      }
    }
  }// end item

  .Legend-text {
    @include fs-scale(font-size, ($bp-row: 16px, $bp-end-scale: 20px), $initial: false);
  }
}// end DoughnutChartGrid
