//------------------------------------------------------------------------
// Stats card
//------------------------------------------------------------------------
.StatsCard {
  $bp-2-col: 580px;
  $bp-3-col: 840px;
  $bp-end-scale: $layout-max-width-bp;
  $card-max-width: 845px;
  // Add 30px beyond card max-width to make it look more intentional
  $bp-max-width: $card-max-width + ($layout-padding-max * 2) + 30px;
  $top-max-width: 444px;
  position: relative;// for bubble decorations

  // Override default Card styles
  &.Card {
    max-width: none;

    @include fs-min-width($bp-max-width) {
      max-width: fs-rem($card-max-width);
    }

    @include fs-min-width($bp-3-col) {
      padding-top: fs-rem(30px);
    }
  }// end Card

  &-top {
    align-items: center;
    display: flex;
    max-width: fs-rem($top-max-width);
  }// end top

  &-label {
    font-size: fs-rem(20px);
    font-weight: $manrope-extrabold;
    line-height: 1.2;
    margin-top: fs-rem(38px);
  }// end label

  &-stat {
    @include fs-scale(font-size, (
      375px: 30px,
      $bp-2-col: (45px 30px),
      $bp-3-col: (45px 30px),
      $bp-end-scale: 45px
    ));
    flex-shrink: 0;
    font-weight: bold;
    letter-spacing: -0.06em;
    line-height: 1;
    margin-right: fs-rem(11px);
  }// end stat

  &-text {
    @include fs-scale(font-size, (
      375px: 16px,
      $bp-2-col: (18px 16px),
      $bp-3-col: (18px 16px),
      $bp-end-scale: 18px
    ));
    line-height: 1.2;
  }// end text

  &-list {
    margin-bottom: fs-rem(30px);
    margin-top: fs-rem(22px);

    @include fs-min-width($bp-2-col) {
      align-items: stretch;
      column-gap: 15px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 20px;
    }

    @include fs-min-width($bp-3-col) {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }// end list

  &-item {
    align-items: center;
    display: flex;

    @include fs-max-width($bp-2-col - 1px) {
      &:not(:first-child) {
        margin-top: fs-rem(15px);
      }
    }
  }// end item

  &-share {
    @include fs-min-width($bp-3-col) {
      max-width: calc(100% - #{$top-max-width + 20px});
      order: -1;
    }
  }// end share

  // Bubble decorations
  &-decorations {
    bottom: -32px;
    height: auto;
    left: 100%;
    margin-left: -28px;
    max-width: 483px;
    position: absolute;
    width: 33vw;
    z-index: -1;

    @include fs-max-width($bp-max-width - 1px) {
      display: none;
    }
  }// end decorations
}// end StatsCard
