//------------------------------------------------------------------------
// Moments of Joy
//------------------------------------------------------------------------
.Joy {
  $bp-wide: 900px;
  // Button vars need to be before $bp-fullwidth
  $button-size: 73px;
  $button-gutter: 20px;
  $bp-fullwidth: $layout-max-width-wide-bp + $button-size + $button-gutter;
  $bp-end-scale: 1440px;
  $image-mobile-width-vw: 55.2vw;
  $image-mobile-max-width: 320px;
  $bp-mobile-image-max-width: $image-mobile-max-width / (fs-strip-unit($image-mobile-width-vw) / 100);
  $image-desktop-width-percent: 36%;
  $image-full-width-percent: 42%;
  $image-aspect-ratio: 207/210;
  @include fs-scale(margin-top, (375px: 40px, $bp-end-scale: 80px));

  // Has ‚ÄúChapterBanner-decorations‚Äù class since styles are the same
  &-decorations {
    position: relative;
  }// end decorations

  &-wrap {
    overflow: hidden;// for bubbles
  }// end wrap

  &-top {
    @include fs-scale(padding-bottom, (375px: 50px, $bp-end-scale: 68px));
    @include fs-scale(padding-top, (375px: 25px, $bp-end-scale: 72px));

    &-heading {
      margin-bottom: fs-rem(15px);
      color: #fff;
    }// end heading

    &-description {
      font-weight: $manrope-semibold;
    }
  }// end top

  &-carousel {
    @include fs-scale(margin-top, (375px: 17px, $bp-end-scale: 40px));

    &-item {
      width: 100%;
    }// end item

    &-content {
      position: relative;
    }// end content

    &-image {
      @include fs-print-hide;
      margin-left: auto;
      margin-right: auto;
      max-width: fs-rem($image-mobile-max-width);
      width: $image-mobile-width-vw;

      @include fs-min-width($bp-wide) {
        max-width: fs-rem(590px);
        width: $image-desktop-width-percent;
      }

      @include fs-min-width($bp-fullwidth) {
        width: $image-full-width-percent;
      }

      img {
        display: block;
        mask-image: fs-svg-url('joy-image-mask');
        mask-size: 100% 100%;
        object-fit: contain;
        width: 100%;
      }
    }// end image

    // List of moments of joy
    &-joys {
      margin-top: fs-rem(25px);

      @include fs-media((min-width: 540px, max-width: $bp-wide - 1px)) {
        columns: 2;
        column-gap: fs-rem(30px);
      }

      // 1. Using a percentage height causes Safari to not layout the grid items correctly
      //    https://github.com/Threespot/trevor-survey/issues/27
      @include fs-min-width($bp-wide) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 100%;
        height: -webkit-fill-available;// [1]
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
      }

      &-item {
        @include fs-scale(font-size, (
          $bp-wide: 18px,
          $bp-fullwidth: 26px,
          $bp-end-scale: 32px
        ), $initial: false);
        break-inside: avoid;
        font-size: fs-rem(26px);
        font-weight: bold;
        letter-spacing: -0.02em;
        line-height: 1.05;
        text-align: center;

        @include fs-max-width($bp-wide - 1px) {
          margin-left: auto;
          margin-right: auto;
          max-width: fs-rem(360px);

          &:not(:last-child) {
            margin-bottom: fs-rem(53px);
          }
        }

        // 2. Safari requires a reflow (e.g. resizing the browser) to lay the items out correctly,
        //    so add a little padding to provide separation between when a reflow doesn‚Äôt happen.
        //    https://github.com/Threespot/trevor-survey/issues/27
        // 3. Note: 100% is actually 50% in the browser since there are 2 columns
        @include fs-min-width($bp-wide) {
          padding-bottom: fs-rem(30px);// [2]
          padding-left: fs-rem(53px);
          padding-right: fs-rem(10px);
          width: 100% - $image-desktop-width-percent;// [3]

          @include fs-min-width($bp-fullwidth) {
            width: 100% - $image-full-width-percent;
          }

          @include fs-min-width($bp-fullwidth) {
            padding-left: 0;
            padding-right: fs-rem(20px);
          }

          &:nth-child(even) {
            justify-self: flex-end;
            padding-left: fs-rem(10px);
            padding-right: fs-rem(53px);

            @include fs-min-width($bp-fullwidth) {
              padding-left: fs-rem(20px);
              padding-right: 0;
            }
          }
        }
      }// end item
    }// end joys
  }// end carousel

  // Flickity overrides
  //---------------------------------
  .flickity {
    &-button {
      background-color: $bg-orange;
      box-shadow: $box-shadow-default;
      color: #fff;
      height: fs-rem($button-size);
      top: ($image-mobile-width-vw / $image-aspect-ratio) / 2;
      width: fs-rem($button-size);

      @include fs-min-width($bp-mobile-image-max-width) {
        top: ($image-mobile-max-width / $image-aspect-ratio) / 2;
      }

      @include fs-min-width($bp-wide) {
        top: 50%;
      }

      &:disabled {
        background-color: $bg-purple;
        cursor: not-allowed;
        opacity: 0.9;
      }

      &:focus {
        box-shadow: none;
        outline: 4px solid currentColor;

        // Hide outline when clicking/tapping
        &:not(:focus-visible) {
          outline: none;
        }
      }

      &.previous {
        left: 0;

        @include fs-min-width($bp-wide) {
          left: -$layout-padding-max + $button-gutter;
        }

        @include fs-min-width($bp-fullwidth) {
          left: -$button-gutter;
          transform: translate(-100%, -50%);
        }
      }// end prev

      &.next {
        right: 0;

        @include fs-min-width($bp-wide) {
          right: -$layout-padding-max + $button-gutter;
        }

        @include fs-min-width($bp-fullwidth) {
          right: -$button-gutter;
          transform: translate(100%, -50%);
        }
      }// end next

      &-icon {
        fill: currentColor;
        height: fs-rem(21px);
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: auto;
      }// end icon
    }// end button
  }// end flickity

  &-bottom {
    @include fs-scale(padding-bottom, (375px: 50px, $bp-end-scale: 76px));
    @include fs-scale(padding-top, (375px: 54px, $bp-end-scale: 64px));
    position: relative;// for bubbles

    &-header {
      @include fs-min-width($bp-wide) {
        align-items: center;
        display: flex;
        justify-content: space-between;
      }

      &-heading {
        color: #fff;
      }

      &-share {
        @include fs-max-width($bp-wide - 1px) {
          margin-top: fs-rem(22px);
        }
      }// end share
    }// end header

    &-list {
      @include fs-scale(margin-top, (375px: 25px, $bp-end-scale: 46px));

      @include fs-min-width(520px) {
        columns: 2;
        column-gap: fs-rem(40px);
      }

      @include fs-min-width(640px) {
        columns: 3;
      }
    }// end list

    &-item {
      font-size: fs-rem(16px);
      font-weight: bold;
      line-height: 1.2;

      &:not(:last-child) {
        margin-bottom: fs-rem(14px);
      }
    }// end item

    &-bubbles {
      @include fs-max-width(1419px) {
        display: none;
      }

      &-left {
        svg {
          left: -107px;
          position: absolute;
          top: -170px;
        }
      }// end left

      &-right {
        svg {
          position: absolute;
          right: -90px;
          top: -190px;
        }
      }// end right
    }// end bubbles
  }// end bottom
}// end Joy
