// Animation helper mixin with sensible defaults
@mixin animation(
  $name,
  $duration: 1s,
  $timing-fn: ease,
  $delay: 0s,
  $count: 1,
  $direction: normal,
  $fill: both
) {
  // animation-delay: $delay;
  // animation-direction: $direction;
  // animation-duration: $duration;
  // animation-fill-mode: $fill;
  // animation-iteration-count: $count;
  // animation-name: $name;
  // animation-timing-function: $timing-fn;
  animation: $name $duration $timing-fn $delay $count $direction $fill;
}

// Animate SVG arrow stroke
//------------------------------------------------------------------------
@keyframes drawArrow {
  0% {
    opacity: 1;// unhide to prevent tiny dot from showing
    stroke-dashoffset: 0;
    stroke-linecap: round;
  }
  100% {
    stroke-dashoffset: 1;
  }
}

@mixin drawArrow(
  $duration: 800ms,
  $timing-fn: ease-out,
  $delay: 600ms,
  $count: 1,
  $direction: reverse,
  $fill: both
) {
  @include animation(drawArrow, $duration, $timing-fn, $delay, $count, $direction, $fill);
}

// Fade in arrow head after line is shown
@keyframes fadeInArrowHead {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@mixin fadeInArrowHead(
  $duration: 150ms,
  $timing-fn: ease,
  $delay: 1.4s,// must equal drawArrow() delay + duration
  $count: 1,
  $direction: normal,
  $fill: both
) {
  @include animation(fadeInArrowHead, $duration, $timing-fn, $delay, $count, $direction, $fill);
}
