//------------------------------------------------------------------------
// Bar chart
//------------------------------------------------------------------------
.BarChart {
  $bp-start-scale: 640px;
  $bp-end-scale: 1024px;
  margin-bottom: 2em;
  margin-top: 2em;

  &-list {}

  &-item {
    &:not(:last-child) {
      margin-bottom: fs-rem(14px);
    }
  }// end item

  &-label {
    @include fs-scale(font-size, ($bp-start-scale: 16px, $bp-end-scale: 20px));
    font-weight: bold;
    line-height: 1.25;
    margin-bottom: fs-rem(12px);
  }// end label

  &-bar {
    align-items: center;
    background-color: $bg-light-gray;
    border-radius: 400px;// arbitrary large value
    break-inside: avoid;
    content: '';
    display: flex;
    overflow: hidden;
    position: relative;
    width: 100%;

    @media print {
      border: 1px solid #000;
    }

    &-inner {
      border-radius: 400px 0 0 400px;// arbitrary large values
      border-top: fs-rem($layout-bar-height-mobile) solid var(--theme-color);
      min-width: 2%;// set 2% as min since 1% looks too small
      position: relative;
      transition: width 1s ease;

      // Required for bar animations (see bar-chart-observer.js)
      // Note: We had to nest the media queries because combining into a single one didn‚Äôt work
      @media not print {
        @media not all and (prefers-reduced-motion: reduce) {
          .js .is-offscreen & {
            width: 0 !important;
          }
        }
      }

      @include fs-min-width($layout-bar-bp) {
        border-top-width: fs-rem($layout-bar-height-desktop);
      }

      .align-right & {
        border-radius: 0 400px 400px 0;// arbitrary large values
        order: 1;
      }
    }// end inner

    &-value {
      @include fs-scale(font-size, ($bp-start-scale: 15px, $bp-end-scale: 20px));
      font-weight: $manrope-extrabold;
      line-height: 1;
      margin-left: 1ch;

      .align-right & {
        margin-left: auto;
        margin-right: 1ch;
      }
    }// end value
  }// end bar

  &-download {
    margin-top: fs-rem(18px);
  }// end download

}// end BarChart
