//------------------------------------------------------------------------
// Butterfly chart (reuses ‚ÄúBarChart‚Äù)
//------------------------------------------------------------------------
.ButterflyChart {
  $bp-wide: $layout-bar-bp;
  $bp-start-scale: 640px;
  $bp-end-scale: 1024px;
  $bar-percent-width-mobile: 47%;
  $label-percent-width-desktop: 18%;
  $bar-percent-width-desktop: (100% - $label-percent-width-desktop) / 2;
  $label-gutter: 24px;
  break-inside: avoid;
  margin-bottom: 2em;
  margin-top: 2em;

  &-labels {
    @include fs-scale(margin-bottom, ($bp-start-scale: 13px, $bp-end-scale: 30px));
    display: flex;
    justify-content: space-between;

    &-label {
      text-align: center;
      width: $bar-percent-width-mobile;

      @include fs-min-width($bp-wide) {
        width: $bar-percent-width-desktop;
      }
    }// end label

    &-spacer {
      order: 0 !important;// override label styles

      @include fs-max-width($bp-wide - 1px) {
        display: none;
      }
    }// end spacer
  }// end labels

  // 1. Equal-height rows with minimum 34px of margin
  &-list {
    @include fs-min-width($bp-wide) {
      display: grid;
      grid-auto-rows: minmax(#{$layout-bar-height-desktop + 34px}, 1fr);// [1]
    }
  }// end list

  &-item {
    align-items: center;
    display: flex;
    justify-content: space-between;

    @include fs-max-width($bp-wide - 1px) {
      flex-wrap: wrap;

      &:not(:first-child) {
        margin-top: fs-rem(20px);
      }
    }
  }// end item

  &-label {
    @include fs-scale(font-size, (
      $bp-start-scale: 16px,
      $bp-wide: (20px 16px),
      $bp-end-scale: 20px
    ));
    font-weight: bold;
    line-height: 1.25;
    text-align: center;

    @include fs-max-width($bp-wide - 1px) {
      margin-bottom: fs-rem(12px);
      width: 100%;
    }

    @include fs-min-width($bp-wide) {
      flex-shrink: 0;
      margin-left: fs-rem($label-gutter);
      margin-right: fs-rem($label-gutter);
      min-width: fs-rem(130px);
      order: 1;
      width: $label-percent-width-desktop;
      word-wrap: break-word;// just in case there is a very long word
    }
  }// end label

  // Note: We‚Äôre reusing the ‚ÄúBarChart-bar‚Äù styles
  &-bar {
    width: $bar-percent-width-mobile;

    @include fs-min-width($bp-wide) {
      width: $bar-percent-width-desktop;

      &:first-child {
        order: 0;
      }

      &:last-child {
        order: 2;
      }
    }
  }// end bar

  &-download {
    @include fs-max-width($bp-wide - 1px) {
      margin-top: fs-rem(20px);
    }
  }// end download
}// end ButterflyChart
