//------------------------------------------------------------------------
// Block quote
//------------------------------------------------------------------------
.Quote {
  $bp-end-scale: 640px;
  $quote-width: 30px;
  @include fs-scale(font-size, (375px: 22px, $bp-end-scale: 28px));
  @include fs-scale(margin-bottom margin-top, (375px: 45px, 1440px: 80px));
  align-items: center;
  break-inside: avoid;
  display: flex;
  flex-direction: column;
  line-height: 1.3;
  margin-left: auto;
  margin-right: auto;
  max-width: fs-rem(918px);
  padding-top: fs-rem(27px);
  text-align: center;

  &-icon {
    --icon-color: #{$bg-orange};

    &.open {
      margin-bottom: fs-rem(20px);
    }

    &.close {
      margin-top: fs-rem(20px);
    }
  }// end icon

  // Use font smoothing as faux light weight so we don‚Äôt have to load the font file just for quotes
  p {
    @include fs-font-smoothing;
  }

  cite {
    @include fs-scale(font-size, (375px: 18px, $bp-end-scale: 20px));
    font-weight: bold;
    line-height: 1.4;
    margin-top: fs-rem(18px);
  }// end cite

}// end Quote
