//------------------------------------------------------------------------
// Tabs
//------------------------------------------------------------------------
.Tabs {
  $bp-end-scale: $layout-max-width-bp;
  $gutter: 14px;
  margin-bottom: fs-rem(40px);

  &-label {
    @include fs-print-hide;
    @include fs-scale(margin-bottom, (375px: 20px, $bp-end-scale: 30px));
    font-size: fs-rem(18px);
    font-weight: $manrope-semibold;
    line-height: 1.2;
  }// end label

  &-list {
    @include fs-print-hide;
    @include fs-scale(margin-bottom, (375px: 48px, $bp-end-scale: 58px));
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin-left: fs-rem(-$gutter);
    margin-top: fs-rem(-$gutter);
  }// end list

  &-item {
    margin-top: fs-rem($gutter);
    padding-left: fs-rem($gutter);
  }// end item

  &-link {
    &[aria-selected="true"] {
      @include fs-font-smoothing;
      --button-color: var(--theme-color);// for border
      background-color: var(--theme-color);
      box-shadow: 0 5px 15px rgba(#000, 0.2);
      color: var(--theme-text-color);

      @include fs-attention {
        text-decoration: underline;
      }

      // Hide underline when link is not hovered, even if it‚Äôs focused/active,
      // unless using the keyboard (like old button-attention() mixin).
      &:not(:hover):not(:focus-visible) {
        text-decoration: none;
      }
    }
  }// end link

  &-section {
    @media print {
      display: block;

      &:before {
        @include h3($color: #000);
        content: attr(aria-label);
        margin-top: 3em;
      }
    }
  }// end section

}// end Tabs
