//------------------------------------------------------------------------
// Line chart
//------------------------------------------------------------------------
.LineChart {
  $bp-wide: 960px;
  $bp-end-scale: $layout-max-width-bp;
  break-inside: avoid;

  &-wrap {
    @include fs-min-width($bp-wide) {
      display: flex;
      justify-content: space-between;
    }
  }// end wrap

  &-legend {
    @include fs-max-width($bp-wide - 1px) {
      display: grid;
      grid-column-gap: fs-rem(30px);
      grid-row-gap: fs-rem(44px);
      grid-template-columns: 1fr 1fr;
      margin-bottom: fs-rem(26px);
    }

    @include fs-min-width($bp-wide) {
      margin-right: fs-rem(60px);
      margin-top: fs-rem(40px);
    }

    .Legend {
      &-item {
        @include fs-max-width($bp-wide - 1px) {
          margin-top: 0;
        }
      }// end item

      &-text {
        @include fs-scale(font-size, (
          375px: 16px,
          $bp-wide: (20px 16px),
          $bp-end-scale: 20px
        ));
      }// end text
    }// end Legend
  }// end legend

  &-chart {
    @include fs-aspect-ratio(1);

    @include fs-min-width($bp-wide) {
      flex-grow: 1;
    }

    // Adjust aspect ratio
    &:before {
      @include fs-min-width(640px) {
        padding-top: 75%;
      }

      @include fs-min-width(800px) {
        padding-top: 66%;
      }
    }

    canvas {
      @include fs-absolute-fill;
    }
  }// end chart

  &-download {
    margin-top: fs-rem(14px);
  }// end download

}// end LineChart
