// -------------------------------------------------------------------------
// Font variables
//
// Note: Quotes arn‚Äôt necessary for most font names https://mothereff.in/font-family
//
// Note: Fallback to system sans-serif fonts since they support multiple weights.
//       All modern operating systems support the following weights:
//       - 900/800/700 Heavy
//       - 600 Bold
//       - 500/400 Regular
//       - 300/200/100 Light
//       (OS X and iOS support more)
//       Demo http://output.jsbin.com/morukug/quiet
//       Cross-browser results https://workflowy.com/s/font-stacks/MnwkgDkYvHT12nYT
//
// Note: ‚Äúcolor-emoji‚Äù is a custom font defined in _fonts.scss
//       See https://www.client9.com/css-color-emoji-stack/ for details.
//------------------------------------------------------------------------

// Fallback fonts
//---------------------------------
$sans-serif: -apple-system, BlinkMacSystemFont, Segoe UI, sans-serif, color-emoji;
$serif: Gerogia, Times New Roman, Times, serif, color-emoji;
$monospace: Operator Mono, Consolas, Menlo, Monaco, monospace, color-emoji;

// Custom web fonts
//---------------------------------
$manrope: Manrope, $sans-serif;
$manrope-semibold: 600;
$manrope-extrabold: 800;

$caveat: Caveat, BradleyHandITCTT-Bold, Noteworthy-Light, Marion-Italic, $serif;

// Caveat fallback styles (requires JS)
// https://meowni.ca/font-style-matcher/
// font-family: BradleyHandITCTT-Bold
// font-size: 36px
// font-style: italic;
// letter-spacing: 0.8px
// line-height: 1.7
// word-spacing: 2px

// Body copy vars
//---------------------------------
$body-copy-font: $manrope;
$body-copy-min-size: 17px;
$body-copy-max-size: 20px;
$body-copy-line-height: 1.45;
