//------------------------------------------------------------------------
// Header
//------------------------------------------------------------------------
.Header {
  $selector: &;
  $bp-full: 1334px;
  $bp-end-scale: 1440px;
  $toggle-icon-width: 32px;
  $toggle-padding: 10px;
  $toggle-width: $toggle-icon-width + ($toggle-padding * 2);
  $toggle-gutter: 30px;
  @include fs-scale(padding-left padding-right, ($layout-nav-bp: 21px, $bp-end-scale: 60px), $initial: false);
  @include fs-print-hide;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: fs-zindex('header');

  @include fs-min-width($layout-nav-bp) {
    height: var(--layout-header-height);
    transition: transform $layout-header-transition-speed $layout-header-transition-easing;

    &.is-below-hero,
    .no-js & {
      background-color: $bg-default;
      box-shadow: $box-shadow-default;
    }

    &.mzp-is-scrolling {}

    &.mzp-is-hidden {
      transform: translate(0, -110%);

      @media screen and (prefers-reduced-motion: reduce) {
        transform: none;
      }

      // Move chapter nav to top when header is hidden
      & + main .ChapterNav {
        @media not screen and (prefers-reduced-motion: reduce) {
          top: 0;
        }
      }
    }
  }

  &-wrap {
    margin-left: auto;
    margin-right: auto;
    max-width: fs-rem(1320px);

    @include fs-min-width($layout-nav-bp) {
      align-items: center;
      display: flex;
      height: 100%;
      position: relative;
    }
  }// end wrap

  &-navToggle {
    $mobile-offset: 21px;
    background-color: $bg-orange;
    border-radius: 50%;
    color: #fff;
    display: block;
    padding: fs-rem(($toggle-padding + 4px) $toggle-padding);

    @include fs-max-width($layout-nav-bp - 1px) {
      left: $mobile-offset - $toggle-padding;
      position: absolute;
      top: $mobile-offset - $toggle-padding;
    }

    @include fs-min-width($bp-full) {
      margin-right: fs-rem($toggle-gutter);
    }

    .is-below-hero &,
    .no-js & {
      color: $c-navy;

      @include fs-max-width($layout-nav-bp - 1px) {
        background-color: #fff;
        box-shadow: $box-shadow-default;
      }

      @include fs-min-width($layout-nav-bp) {
        background-color: transparent;
      }
    }

    &[aria-expanded="false"] {
      &:before,
      &:after {
        transition: transform 150ms ease-in-out;
      }

      @include button-attention {
        .icon {
          &:before {
            transform: translateY(-2px);
          }

          &:after {
            transform: translateY(2px);
          }
        }
      }
    }

    .icon {
      @include fs-burger(
        $color: currentColor,
        $gutter: 6px,
        $height: 4px,
        $transition-duration: 200ms,
        $width: 32px
      );
      color: inherit;
      display: block;

      &,
      &:before,
      &:after {
        border-radius: 100px;// arbitrary large number
      }
    }// end icon
  }// end navToggle

  &-logo {
    @include link-underline-hover;
    align-items: center;
    display: none;// only shown in desktop after scrolling past hero
    justify-content: space-between;

    .is-below-hero &,
    .no-js & {
      --theme-text-color: #{$c-navy};
    }

    @include fs-min-width($layout-nav-bp) {
      margin-left: fs-rem($toggle-gutter);

      .is-below-hero &,
      .no-js & {
        display: flex;
      }
    }

    @include fs-min-width($bp-full) {
      margin-left: 0;
    }

    &-text {
      color: $c-navy;
      font-size: fs-rem(20px);
      font-weight: bold;
      line-height: 1;
    }// end text

    &-image {
      display: block;
      height: auto;
      margin-left: fs-rem(19px);
      width: fs-rem(42px);
    }// end image
  }// end logo

  &-utilNav {
    align-items: center;
    display: flex;
    justify-content: space-between;

    @include fs-max-width($layout-nav-bp - 1px) {
      display: none;
    }

    @include fs-min-width($layout-nav-bp) {
      margin-left: auto;// right align
    }

    &-link {
      &:not(:first-child) {
        margin-left: fs-rem(42px);
      }

      &:not(.btn-border) {
        @include link-border-animate;
        color: $c-navy;
      }

      // Override button background-color when in hero
      &.btn-border {
        background-color: #fff;

        @include fs-attention {
          @include fs-font-smoothing;
          background-color: $bg-navy;
          color: #fff;
        }

        // Custom styles for when button overlaps the hero
        .js #{$selector}:not(.is-below-hero) & {
          &:not(:hover):not(:active):not(:focus) {
            border-color: #fff;
          }
        }
      }
    }// end link
  }// end utilNav

}// end Header
