    @error 'üî¥  $svg-map doesn‚Äôt exist';
    @return false;
  }

  @if not type-of($svg-map) == 'map' {
    @error 'üî¥  $svg-map isn‚Äôt a valid Sass map';
    @return false;
  }

  @if not map-has-key($svg-map, $name) {
    @error 'üî¥  icon ‚Äú#{$name}‚Äù not found in $svg-map';
    @return false;
  }

  $svg: map-get($svg-map, $name);

