//------------------------------------------------------------------------
// Nav (functions like modal window)
//------------------------------------------------------------------------
.Nav {
  $selector: &;
  $bp-end-scale: 640px;
  $max-width: 490px;
  $close-speed: 300ms;
  $open-speed: 450ms;
  $easing: fs-easing('easeOutCubic');
  $link-vert-padding: 12px;
  $link-vert-margin: 30px - ($link-vert-padding * 2);
  $z-index: fs-zindex('nav');
  bottom: 0;
  left: 0;
  max-height: 0;// iOS fix
  overflow: auto;// allows scrollig when content exceeds viewport height
  -webkit-overflow-scrolling: touch;// iOS ‚Äúmomentum‚Äù scrolling
  position: fixed;
  right: 0;
  top: 0;
  transition: background-color 150ms linear,
              max-height 0s linear $close-speed,
              visibility 0s linear $close-speed;
  visibility: hidden;// hide from screen readers and keyboards until active
  z-index: $z-index - 1;// must be greater than overlay

  // Styles will be applied when URL hash matches modal ID attribute
  &:not([aria-hidden]):target,
  &[aria-hidden="false"] {
    max-height: 100vh;// can‚Äôt set to ‚Äúnone‚Äù because that can't be transitioned
    opacity: 1;
    transition: background-color 0ms linear,
                max-height 0s linear,
                visibility 0s linear;
    visibility: visible;
    z-index: $z-index;
  }

  // Modal content wrapper
  &-wrap {
    @include fs-font-smoothing;
    @include fs-scale(padding-left padding-right, (375px: 34px, $bp-end-scale: 68px));
    @include fs-scale(padding-top, (375px: 40px, $bp-end-scale: 68px));
    background-color: $bg-violet;
    color: #fff;
    padding-bottom: fs-rem(78px);
    text-shadow: 0 0 3px rgba($bg-purple, 0.5);// improve legibility
    z-index: 2;// above bubbles

    .js & {
      left: 0;
      min-height: 100%;
      position: absolute;
      top: 0;
      transform: translateX(-100%);
      transition: transform $close-speed $easing;
      width: 100%;
      z-index: $z-index + 1;

      @include fs-min-width($max-width + 40px) {
        max-width: fs-rem($max-width);
      }
    }

    #{$selector}:not([aria-hidden]):target &,
    #{$selector}[aria-hidden="false"] & {
      transform: translateX(0);
      transition: transform $open-speed $easing;
    }
  }// end wrap

  &-close {
    $padding: 10px;
    color: currentColor;
    display: inline-block;
    margin-bottom: fs-rem(30px - $padding);
    margin-left: fs-rem(-$padding);
    margin-top: fs-rem(-$padding);
    padding: fs-rem(10px);

    @include fs-attention {
      transform: scale(1.1);
    }

    svg {
      display: block;
      fill: currentColor;
    }
  }// end close

  &-primary {
    &-item {
      &:not(:first-child) {
        margin-top: fs-rem($link-vert-margin);
      }
    }// end item

    &-link {
      @include fs-scale(font-size, (375px: 21px, $bp-end-scale: 24px));
      @include link-underline-hover;
      color: currentColor;
      display: inline-block;
      font-weight: $manrope-extrabold;
      letter-spacing: 0.1px;
      line-height: 1.2;
      padding-bottom: fs-rem($link-vert-padding);
      padding-top: fs-rem($link-vert-padding);
    }// end link
  }// end primary

  &-utilNav {
    $horz-gutter: 38px;
    $row-gap: 25px;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: fs-rem(32px - $row-gap);

    &-link {
      --button-color: #fff;
      --button-hover-color: #{$c-navy};
      font-size: fs-rem(16px);
      font-weight: $manrope-extrabold;
      margin-top: fs-rem($row-gap);

      &:not(:last-child) {
        margin-right: fs-rem($horz-gutter);
      }

      &:not(.btn-border) {
        @include link-underline-hover;
        color: currentColor;
        line-height: 1.2;
      }
    }// end link
  }// end utilNav

  &-contact {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    font-size: fs-rem(24px);
    font-weight: $manrope-semibold;
    line-height: 1.5;
    margin-top: fs-rem(35px);

    &-phone {
      @include link-underline-hover;
      margin-top: fs-rem(17px);
    }// end phone

    &-cta {
      --button-color: #{$c-blue};
      margin-top: fs-rem(17px);
    }// end cta
  }// end contact

  &-bubbles {
    svg {
      bottom: 22px;
      height: auto;
      left: -67px;
      position: absolute;
      width: 196px;
      z-index: -1;// below content
    }
  }// end bubbles
}// end Nav
