//------------------------------------------------------------------------
// Methodology
//------------------------------------------------------------------------
.Methodology {
  $bp-header-wide: 680px;
  $bp-end-scale: 1440px;
  @include fs-scale(margin-top, ($bp-header-wide: 60px, $bp-end-scale: 122px));
  margin-top: fs-rem(20px);

  &-header {
    $chart-width-mobile: 88px;
    $chart-width-desktop: 198px;
    align-items: flex-start;
    display: flex;

    @include fs-max-width($bp-header-wide - 1px) {
      flex-direction: column;
    }

    @include fs-min-width($bp-header-wide) {
      justify-content: space-between;
    }

    &-text {
      max-width: fs-rem(810px);
    }// end text

    &-title {
      @include fs-scale(font-size, (360px: 23px, $layout-text-scale-end-bp: 45px));
      font-weight: $manrope-extrabold;
      line-height: 1.2;
      margin-bottom: fs-rem(12px);

      @include fs-max-width($bp-header-wide - 1px) {
        padding-right: fs-rem($chart-width-mobile + 10px);
      }
    }// end title

    &-description {
      @include fs-scale(font-size, (360px: 16px, $layout-text-scale-end-bp: 24px));
      font-weight: $manrope-semibold;
      line-height: 1.2;
    }// end description

    &-chart {
      @include fs-max-width($bp-header-wide - 1px) {
        align-self: flex-end;
        margin-bottom: fs-rem(-10px);
        max-width: fs-rem($chart-width-mobile);
        order: -1;
      }

      @include fs-min-width($bp-header-wide) {
        flex-shrink: 0;
        margin-left: fs-rem(50px);
        max-width: fs-rem($chart-width-desktop);
        width: 18%;
      }

      svg {
        display: block;
        height: auto;
        width: 100%;
      }
    }// end chart
  }// end header

  &-wrap {
    @include fs-scale(margin-bottom, (375px: 40px, $bp-end-scale: 100px));
    margin-top: fs-rem(20px);
  }// end wrap

  &-toggle {
    @include fs-print-hide;
  }

  &-content {
    @include expandable-height();

    & > *:first-child {
      margin-top: 2em;
    }

    &-text {
      margin-bottom: fs-rem(52px);
    }
  }// end content

}// end Methodology
