// -------------------------------------------------------------------------
// Link Styles
// -------------------------------------------------------------------------
// Base link styles, excluding color
@mixin link-base {
  transition: all 150ms ease;
  text-decoration: none;
}

@mixin link-underline() {
  text-decoration: underline;

  @include fs-all-states {
    color: $c-purple;
  }

  @include fs-attention {
    text-decoration: none;
  }
}

@mixin link-underline-hover($underline-color: var(--theme-text-color, currentColor), $thickness: 2px) {
  @include fs-attention {
    @supports (text-decoration-thickness: 2px) {
      text-decoration-color: $underline-color;
      text-decoration-line: underline;
      text-decoration-thickness: $thickness;
      text-underline-offset: 3px;
    }
  }
}

@mixin link-border-animate($thickness: 2px) {
  position: relative;

  @include button-attention {
    &:after {
      opacity: 1;
      transform: translateY(3px);
    }
  }

  &:after {
    border-top: $thickness solid currentColor;
    bottom: 0;
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    transform: translateY(1px);
    transition: all 150ms ease-in-out;
    width: 100%;
  }
}

// Custom named link styles
//---------------------------------
@mixin link-richtext() {
  @include link-base;
  @include link-underline;
}
