//------------------------------------------------------------------------
// Key Findings
//------------------------------------------------------------------------
.KeyFindings {
  $bp-wide: 670px;
  $bp-end-scale: 1440px;
  @include fs-scale(padding-bottom, ($bp-wide: 30px, $bp-end-scale: 65px));
  overflow: hidden;// prevent horz scroll due to bubbles
  position: relative;

  &-bubbles {
    @include fs-print-hide;

    @include fs-max-width($bp-wide - 1px) {
      display: none;
    }

    svg {
      bottom: 22px;
      height: auto;
      position: absolute;
      right: -40px;
      width: 309px;
      z-index: -1;
    }
  }// end bubbles
}// end KeyFindings
