//------------------------------------------------------------------------
// Chapter navigation
//------------------------------------------------------------------------
.ChapterNav {
  $bp-row: 740px;
  $gutter: 30px;
  $link-border-width: 1px;
  $link-border-width-hover: 4px;
  $link-vert-padding: 6px;
  @include fs-print-hide;
  margin-bottom: fs-rem(57px);
  padding-bottom: fs-rem(36px + $link-border-width-hover);
  padding-top: fs-rem(40px);
  position: relative;
  z-index: fs-zindex('header') - 1;

  @include fs-min-width($layout-nav-bp) {
    box-shadow: $box-shadow-default;
    margin-bottom: fs-rem(80px);
    padding-bottom: fs-rem(6px + $link-border-width-hover);
    padding-top: fs-rem(6px);
    position: sticky;
    top: var(--layout-header-height);
    transition: top $layout-header-transition-speed $layout-header-transition-easing;
  }

  @media print {
    position: static;
  }

  &-wrap {
    @include fs-min-width($bp-row) {
      align-items: baseline;
      display: flex;
    }
  }// end wrap

  &-heading {
    line-height: 1.2;
    font-size: fs-rem(18px);
    font-weight: $manrope-semibold;
    flex-shrink: 0;
  }// end heading

  &-list {
    display: flex;

    @include fs-max-width($bp-row - 1px) {
      align-items: flex-start;
      flex-direction: column;
    }

    @include fs-min-width($bp-row) {
      align-items: center;
      flex-wrap: wrap;
      row-gap: 20px;
    }
  }// end list

  &-item {
    @include fs-max-width($bp-row - 1px) {
      margin-top: fs-rem(23px);

      &:not(:first-child) {
        margin-top: fs-rem(13px);
      }
    }

    @include fs-min-width($bp-row) {
      padding-left: fs-rem($gutter);
    }
  }// end item

  &-link {
    color: var(--theme-text-color);
    display: block;
    font-size: fs-rem(18px);
    font-weight: bold;
    line-height: 1.2;
    position: relative;
    padding-top: fs-rem($link-vert-padding);
    padding-bottom: fs-rem($link-vert-padding);

    &:after {
      border-top: $link-border-width solid currentColor;
      content: '';
      position: absolute;
      left: 0;
      top: 100%;
      transition: border-width 150ms ease-in-out;
      width: 100%;
    }

    @include fs-attention {
      &:after {
        border-top-width: $link-border-width-hover;
      }
    }
  }// end link
}// end ChapterNav
