//------------------------------------------------------------------------
// Chart image download link
//------------------------------------------------------------------------
.Download {
  @include fs-print-hide;
  display: flex;
  justify-content: flex-end;

  &-link {
    @include link-underline-hover($underline-color: currentColor);
    align-items: center;
    display: flex;
    color: $c-navy;
    font-size: fs-rem(14px);
    font-weight: bold;

    @include fs-attention {
      .icon {
        transform: scale(1.1);
      }
    }

    .icon {
      --icon-bg-color: var(--theme-color);
      --icon-color: var(--theme-text-color);
      flex-shrink: 0;
      margin-left: fs-rem(10px);
      transition: all 150ms ease-in-out;
    }
  }// end link
}// end Download
