//------------------------------------------------------------------------
// Card grid
//------------------------------------------------------------------------
.CardGrid {
  $bp-2-col: 730px;
  $bp-3-col: 1024px;
  $col-gap: 34px;
  $row-gap: 38px;
  $three-col-gap: 26px;
  $shadow-height: 12px;// magic number related to $box-shadow-default
  $bubbles-height: 274px;
  $bubbles-width: 257px;
  margin-bottom: calc(1em - #{fs-rem($shadow-height)});
  margin-top: 1em;
  padding-bottom: fs-rem($shadow-height);
  position: relative;// for bubbles

  // Note: Double the column numbers so we can center the last odd item
  @include fs-min-width($bp-2-col) {
    align-items: stretch;
    column-gap: $col-gap;
    display: grid;
    grid-template-columns: repeat(4, 1fr);// [1]
    row-gap: $row-gap;

    // Left bubbles
    &:before {
      @include fs-print-hide;
      background-image: fs-svg-url('card-grid-bubbles');
      content: '';
      height: $bubbles-height;
      left: -200px;
      position: absolute;
      top: 62px;
      width: $bubbles-width;
      z-index: -1;
    }

    // We need to hide the bubbles before the Methodology section
    &.no-bubbles {
      &:before {
        display: none;
      }
    }

    .Card {
      grid-column: span 2;
      margin-top: 0 !important;

      // Last child, but not the only child
      &:last-child:not(:first-child) {
        // Right bubbles
        &:after {
          @include fs-print-hide;
          background-image: fs-svg-url('card-grid-bubbles');
          content: '';
          height: $bubbles-height;
          position: absolute;
          right: -194px;
          top: -26px;
          transform: rotate(180deg);
          width: $bubbles-width;
          z-index: -1;
        }
        // Center last card when there‚Äôs an odd number
        &:nth-child(odd) {
          grid-column-end: -2;
        }
      }

      // Center single card
      &.is-centered {
        grid-column-end: -2;
      }
    }
  }

  &[data-columns="3"] {
    @include fs-min-width($bp-2-col) {
      column-gap: $three-col-gap;
      row-gap: $three-col-gap;
    }

    @include fs-min-width($bp-3-col) {
      grid-template-columns: repeat(3, 1fr);

      .Card {
        grid-column: auto;

        // Don‚Äôt center list item in 3-col layout
        &:nth-last-child(1):nth-child(odd) {
          grid-column-end: auto;
        }
      }
    }
  }// end 3-col

}// end CardGrid
