// -------------------------------------------------------------------------
// Button Styles
// -------------------------------------------------------------------------
@mixin button-reset {
  background-color: transparent;
  background-image: none; // for Firefox mobile
  border: 0;
  color: inherit;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
  text-align: center;
  transition: background-color 150ms ease-in-out,
              background-position 150ms ease,
              border 150ms ease-in-out,
              color 150ms ease-in-out;
}

@mixin button-base {
  @include button-reset;
  font-size: fs-rem(16px);// Was 20px in design but Facit appears 1px larger in the browser
  font-weight: $manrope-extrabold;
  padding: fs-rem(14px 16px);
}

@mixin button-solid($color: $c-navy, $bg-color: #fff) {
  background-color: var(--button-bg-color, $bg-color);
  border-radius: 500px;// arbitrary large number
  color: var(--button-color, $color);
  font-weight: bold;
  text-shadow: none;

  @include button-attention {
    background-color: var(--button-color, $color);
    color: var(--button-bg-color, $bg-color);
    text-decoration: none;// for link tags
  }
}

@mixin button-border($color: $c-navy, $hover-color: #fff) {
  background-color: transparent;
  border: 2px solid var(--button-color, $color);
  border-radius: 500px;// arbitrary large number
  color: var(--button-color, $color);

  // Example: Switch to white on dark backgrounds (requires setting up custom color palette)
  // TODO: Automate this by including dark/light info in $block-colors map
  // .has-navy-background-color & {
  //   --button-color: #fff;
  //   --button-hover-color: navy;
  // }

  @include button-attention {
    @include fs-font-smoothing;
    background-color: var(--button-color, $color);
    color: var(--button-hover-color, $hover-color);
    text-decoration: none;// for link tags
    text-shadow: none;
  }
}

// Used by expand toggles (intro, methodology)
@mixin button-toggle() {
  $circle-size: 40px;
  $icon-width: 12px;
  $icon-thickness: 2px;
  align-items: center;
  display: flex;
  font-weight: bold;// override default button styles
  padding: 0;// override default button styles

  @include fs-attention {
    text-decoration: underline;
  }

  // Hide underline when link is not hovered, even if it‚Äôs focused/active,
  // unless using the keyboard (like old button-attention() mixin).
  &:not(:hover):not(:focus-visible) {
    text-decoration: none;
  }

  // Expanded state
  &[aria-expanded="true"] {
    .icon {
      background-color: $bg-light-gray;
      color: $c-navy;

      // Un-rotate the vertical line in plus sign
      &:after {
        transform: translate(-50%, -50%);
      }
    }
  }

  .icon {
    background-color: $bg-violet;
    border-radius: 50%;
    color: #fff;
    height: fs-rem($circle-size);
    margin-right: fs-rem(10px);
    position: relative;
    transition: background-color 150ms ease-in-out, color 150ms ease-in-out;
    width: fs-rem($circle-size);

    // Use pseudo elements to create the plus/minus sign
    &:before,
    &:after {
      border-top: $icon-thickness solid currentColor;
      content: '';
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: transform 150ms ease-in-out;
      width: fs-rem($icon-width);
    }

    &:after {
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }// end icon
}
