//------------------------------------------------------------------------
// Introduction
//------------------------------------------------------------------------
.Intro {
  $bp-end-scale: $layout-max-width-bp;
  @include fs-scale(padding-bottom, (375px: 34px, $bp-end-scale: 136px));
  @include fs-scale(padding-top, (375px: 34px, $bp-end-scale: 69px));
  overflow: hidden;// for bubbles
  position: relative;// for bubbles

  &-content {
    &-collapsed {
      @include fs-max-width($layout-intro-content-bp - 1px) {
        @include expandable-height();
      }
    }// end collapsed
  }// end content

  // Note: Has ‚Äú.btn-toggle‚Äù class
  &-toggle {
    @include fs-print-hide;

    &[aria-expanded="true"] {
      margin-top: 1em;
    }

    @include fs-min-width($layout-intro-content-bp) {
      display: none;
    }
  }// end toggle

  &-signature {
    margin-top: fs-rem(43px);

    &-name {
      @include fs-scale(font-size, (375px: 28px, $bp-end-scale: 32px));
      font-weight: $manrope-extrabold;
      line-height: 1.2;
    }// end name

    &-title {
      @include fs-scale(font-size, (375px: 20px, $bp-end-scale: 24px));
      display: block;
      font-size: fs-rem(20px);
      line-height: 1.2;
      margin-top: fs-rem(4px);
    }// end title

    &-image {
      display: block;
      margin-top: fs-rem(16px);
    }// end image
  }// end signature

  &-bubbles {
    @include fs-print-hide;

    svg {
      display: block;
      height: auto;
      width: 100%;
    }

    &-left {
      bottom: 0;
      left: -113px;
      position: absolute;
      max-width: fs-rem(413px);
      width: 28vw;

      @include fs-max-width(1279px) {
        display: none;
      }
    }// end left

    &-right {
      position: absolute;
      right: -27px;
      top: -30px;
      max-width: fs-rem(297px);
      width: 28vw;

      @include fs-max-width($layout-intro-content-bp - 1px) {
        display: none;
      }
    }// end right
  }// end
}// end Intro
