//------------------------------------------------------------------------
// Doughnut chart
//------------------------------------------------------------------------
.DoughnutChart {
  $bp-wide: 540px;
  $bp-end-scale: 768px;
  $legend-gutter: 36px;

  &-title {
    @include fs-scale(font-size, (375px: 18px, $bp-end-scale: 25px));
    font-weight: $manrope-extrabold;
    line-height: 1.2;
    margin-bottom: fs-rem(37px);

    @include fs-min-width($bp-wide) {
      margin-bottom: fs-rem(43px);
    }
  }// end title

  &-wrap {
    @include fs-min-width($bp-wide) {
      display: flex;
      justify-content: space-between;
    }
  }// end wrap

  &-legend {
    .Legend-text {
      @include fs-scale(font-size, (
        375px: 16px,
        $bp-wide: (20px 16px),
        $bp-end-scale: 20px
      ));
    }
  }// end legend

  // Wrapper around <canvas> element
  &-chart {
    aspect-ratio: 1;

    @include fs-max-width($bp-wide - 1px) {
      margin-top: fs-rem(32px);
      max-width: fs-rem(320px);
    }

    @include fs-min-width($bp-wide) {
      margin-left: fs-rem(30px);
      flex-shrink: 0;
      width: 51%;
    }

    @media print {
      canvas {
        object-fit: contain;
      }
    }
  }// end chart

  &-download {
    margin-top: fs-rem(18px);
  }// end download

}// end DoughnutChart
