//------------------------------------------------------------------------
// Footer
//------------------------------------------------------------------------
.Footer {
  $bp-services-cols: 640px;
  $bp-wide: 1024px;
  $bp-end-scale: 1440px;
  @include fs-scale(padding-top, (375px: 20px, $bp-end-scale: 160px));
  @include fs-scale(padding-bottom, (375px: 64px, $bp-end-scale: 100px));
  content-visibility: auto;// apply to offscreen content to reduce render time on load

  &-wrap {
    @include fs-min-width($bp-wide) {
      display: flex;
      justify-content: space-between;
    }
  }

  &-content {}

  &-body {
    margin-top: fs-rem(20px);

    @include fs-min-width($bp-wide) {
      font-size: fs-rem(20px);
    }
  }

  &-intro {
    @include fs-scale(font-size, (
      375px: 16px,
      $bp-wide: (28px 22px),
      $layout-max-width-wide-bp: 20px
    ));
    font-weight: bold;
  }

  &-logo {
    display: block;
    margin-bottom: fs-rem(20px);
    max-width: fs-rem(277px);
    width: 100%;

    @include fs-min-width($bp-wide) {
      margin-bottom: fs-rem(40px);
      max-width: 540px;
    }
  }

  &-social {
    $vert-margin: 20px;

    &-title {
      font-size: fs-rem(20px);
      font-weight: $manrope-extrabold;
      line-height: 1.2;
      margin-bottom: fs-rem(16px);
      margin-top: fs-rem(30px);
    }

    &-links {
      display: flex;
      flex-wrap: wrap;
      margin-top: fs-rem(-$vert-margin);

      svg {
        margin-right: fs-rem(10px);
      }
    }

    &-link {
      @include link-underline-hover($thickness: 1px);
      --icon-color: var(--theme-text-color);
      align-items: center;
      color: #fff;
      display: flex;
      font-weight: $manrope-semibold;
      margin-top: fs-rem($vert-margin);

      &:not(:last-child) {
        margin-right: fs-rem(16px);
      }
    }
  }// end social

  &-services {
    margin-top: fs-rem(34px);

    @include fs-media((min-width: $bp-services-cols, max-width: $bp-wide - 1px)) {
      align-items: flex-start;
      column-gap: 30px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin-top: 50px;
      row-gap: 40px;
    }

    @include fs-min-width($bp-wide) {
      flex-shrink: 0;
      margin-top: fs-rem(8px);
      padding-left: 10%;
      width: 52%;
    }

    &-item {
      &:not(:first-child) {
        margin-top: fs-rem(32px);
      }

      @include fs-media((min-width: $bp-services-cols, max-width: $bp-wide - 1px)) {
        margin-top: 0 !important;
      }

      @include fs-min-width($bp-wide) {
        align-items: center;
        display: flex;
      }
    }

    &-icon {
      @include fs-max-width($bp-wide - 1px) {
        margin-bottom: fs-rem(23px);
        max-width: fs-rem(100px);
      }

      @include fs-min-width($bp-wide) {
        flex-shrink: 0;
        margin-right: fs-rem(27px);
        width: 32%;
      }

      svg {
        display: block;
        max-width: 100%;

        @include fs-min-width($bp-wide) {
          margin-left: auto;
          margin-right: auto;
        }
      }
    }

    &-description {
      font-weight: $manrope-semibold;
      margin-top: fs-rem(8px);
    }

    &-link {
      color: #fff;
      display: inline-block;
      font-weight: $manrope-extrabold;
      font-size: fs-rem(20px);
      line-height: 1.2;
      text-decoration: underline;

      @supports (text-decoration-thickness: 2px) {
        text-decoration-color: currentColor;
        text-decoration-line: underline;
        text-decoration-thickness: 1px;
        text-underline-offset: 3px;
      }

      @include fs-attention {
        text-decoration: none;
      }
    }
  }// end services
}// end Footer
