// -------------------------------------------------------------------------
// Color variables
//
// Use variable names that describe the color. Avoid additional abstraction
// unless absolutely necessary (e.g. themeing)
// http://bensmithett.com/stop-using-so-many-sass-variables/
//
// Can‚Äôt think of another name for a similar color? Try this site:
// http://chir.ag/projects/name-that-color/
//
// Note: We‚Äôre using the ‚Äúpostcss-custom-properties‚Äù plugin to add
//       fallbacks for browsers that don‚Äôt support CSS vars.
//       https://caniuse.com/css-variables
// -------------------------------------------------------------------------

// Background colors
//---------------------------------
$bg-blue-green: #397275;// only used in some pie charts
$bg-blue: #00479c;// ‚ÄúCobalt Blue‚Äù in Figma
// $bg-deep-teal: #003a48;
$bg-gold: #f9c058;
$bg-light-blue: #678fbe;// only used in some pie charts
$bg-light-gray: #e7e7e7;
$bg-light-teal: #8dc8cb;// only used in some pie charts
$bg-navy: #101066;// ‚ÄúIndigo‚Äù in Figma
$bg-offwhite: #f9f9f9;
$bg-orange: #ff5a3d;// ‚ÄúTangerine‚Äù in Figma
$bg-purple: #40009a;
$bg-teal: #005e67;// ‚ÄúBlue Green‚Äù in Figma
$bg-violet: #7155ff;
$bg-yellow: #ffd215;// ‚ÄúCanary‚Äù in Figma

// Text colors
//---------------------------------
$c-blue: $bg-blue;
$c-light-gray: #767676;
$c-navy: $bg-navy;
$c-purple: $bg-purple;
$c-red: #eb0c00;

// Theme Colors (used in _background-helpers.scss)
//---------------------------------
$theme-colors: (
  'blue': $bg-blue,
  'blue-green': $bg-blue-green,
  'gold': $bg-gold,
  'light-blue': $bg-light-blue,
  'light-teal': $bg-light-teal,
  'navy': $bg-navy,
  'orange': $bg-orange,
  'purple': $bg-purple,
  'teal': $bg-teal,
  'violet': $bg-violet,
  'yellow': $bg-yellow,
);

// Default colors
//---------------------------------
$bg-default: $bg-offwhite;
$c-default: $c-navy;

// Note: We‚Äôre CSS variables to make it easier to switch color themes.
//       We‚Äôre using the ‚Äúpostcss-custom-properties‚Äù plugin to add
//       fallbacks for browsers that don‚Äôt support CSS vars.
//       https://caniuse.com/css-variables
// $bg-default: var(--bg-default);
// $c-default: var(--c-default);

// Shadows
//---------------------------------
$box-shadow-default: 0 3px 8px rgba(#000, 0.17);// originally drop-shadow(0 4px 10px rgba(#000, 0.15)) in Figme
